import gsap from 'gsap'

export default {
    data () {
        return {
            t1: null,
            offsetTop: 0,
            scrollTop: 0,
            interval: null,
            isDrag: false,
            isDragging: false,
            dir: 'right',
            touchX: 0,
        }
    },
    mounted () {
        this.t1 = gsap.timeline()
        this.offsetTop = this.$refs.gallery.scrollHeight - window.innerWidth
        this.autoFly()
        this.initInterval()
    },
    beforeDestroy () {
        window.clearInterval(this.interval)
    },
    methods: {
        drag (e) {
            this.isDragging = true
            if (!this.isDrag) {
                return
            }

            let moveX
            if (e.touches) {
                moveX = e.touches[0].clientX - this.touchX
                this.touchX = e.touches[0].clientX
            } else {
                moveX = e.movementX
            }

            this.$refs.gallery.scrollBy(0, -moveX)
            this.scrollTop = this.$refs.gallery.scrollTop
            this.dir = e.movementX >= 0 ? 'left' : 'right'

            if (this.scrollTop < 10) {
                this.dir = 'right'
            } else if (this.scrollTop > this.offsetTop - 10) {
                this.dir = 'left'
            }
        },
        enableDrag (e) {
            this.isDrag = true
            this.killFly()
            this.killInterval()

            if (e.touches) {
                this.touchX = e.touches[0].clientX
            }
        },
        disableDrag () {
            this.isDragging = false
            if (!this.isDrag) {
                return
            }
            this.isDrag = false
            this.t1 = gsap.timeline()
            this.autoFly(this.dir)
            this.initInterval()
        },
        autoFly (dir) {
            if (dir === 'left') {
                this.t1.to(this, { scrollTop: 0, duration: 120 * this.scrollTop / this.offsetTop, ease: 'linear' })
            } else {
                this.t1.to(this, { scrollTop: this.offsetTop, duration: 120 * (1 - this.scrollTop / this.offsetTop), ease: 'linear' })
            }
        },
        killFly () {
            this.t1.kill()
        },
        initInterval () {
            this.interval = setInterval(() => {
                this.$refs.gallery.scrollTo(0, this.scrollTop)
            }, 20)
        },
        killInterval () {
            window.clearInterval(this.interval)
        },
    },
}
